import Vue from 'vue';
import VueRouter from 'vue-router'

import home from './page/home.vue'
import about from './page/about.vue'
import error from './page/404.vue'

Vue.use(VueRouter)

const router = new VueRouter({
	//mode: 'history',//
	routes: [{
			path: '/',
			component: home
		}, {
			path: '/about',
			component: about
		}, {
			path: '*',
			component: error
		}
	]
});

export default router;


